@import '../../assets/scss/color';
@import '../../assets/scss/fonts';




















































































































































































































































































































































































































































































































.fileType {
  #spreadsheet-execl {
    width: 100%;
    .x-spreadsheet-toolbar {
      width: 1000px !important;
    }
  }
  .create_button {
    margin-left: 10px;
    padding: 7px 9px;
    color: #3674f9;
  }
  .el-form--inline {
    .el-form-item--small {
      width: 48%;
    }
    .el-form-item--small:last-child {
      width: 99%;
    }
  }

  .el-dialog__body {
    .el-form-item {
      .el-select {
        width: 100%;
      }
    }
  }
  .defineForm {
    .tipsClass {
      .el-form-item__label:before {
        content: '*';
        color: #f56c6c;
        margin-right: 4px;
      }
    }
    .msgClass {
      color: #f56c6c;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
      position: absolute;
      top: 100%;
      left: 0;
    }
    .errorClass {
      .el-input__inner {
        border: 1px solid #f56c6c;
      }
    }
  }
}
